body {
  font-family: "Montserrat", sans-serif;
  font-smoothing: antialiased;
  background-color: #fac564;
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/5908/food.png);
}

/* body{
  background-color: #f9ead9;
} */
.notes-header {
  position: relative;
}

.page-header-row {
  text-align: center;
  height: 100px;
  /* background-image: url('./us.jpg'); */
}

.header img {
  float: left;
  width: 100px;
  height: 100px;
  background: #555;
}

.page-header {
  align-items: center;
  margin-top: 5px;
  font-size: 50px;
  color: #032151;
  font-family: Comic Sans MS;
}

.action-button-row {
  text-align: center;
  justify-content: space-between;
}

.add-entry-sh-btn {
  margin-top: 10px;
  font-size: 14px;
  margin-left: 0px;
  margin-right: 20px;
  font-weight: bolder;
}

.add-entry-so-btn {
  margin-top: 10px;
  font-size: 14px;
  margin-left: 3px;
  margin-right: 0px;
  font-weight: bolder;
}

.glyphicon-plus-sign {
  padding-right: 5px;
}

.notes-deck {
  margin-top: 10px;
}

.note {
  background-color: #f4f4f4;
  padding: 5px;
  transition: all .2s ease-in-out;
}

.note_shivani {
  margin-top: 3px;
  margin-bottom: 3px;
  margin-left: 3px;
  background-color: #bcd6ff;
  padding: 5px;
  font-size: 20px;
  padding-left: 15px;
  border-radius: 4px;
  transition: all .2s ease-in-out;
  box-shadow: 0 0 6px 1px #f4f4f4;
  margin-bottom: 5px;
}

.note_shivani:hover {
  background-color: #bcd6ff;
  padding: 5px;
  padding-left: 15px;
  transform: scale(1.09);
}

.note_sourabh {
  margin-left: 3px;
  margin-top: 3px;
  margin-bottom: 3px;
  background-color: #efa083;
  padding: 5px;
  padding-left: 15px;
  font-size: 20px;
  padding: 5px;
  border-radius: 4px;
  transition: all .2s ease-in-out;
  box-shadow: 0 0 6px 1px #f4f4f4;
}

.note_sourabh:hover {
  background-color: #efa083;
  padding: 5px;
  padding-left: 15px;
  transform: scale(1.09);
}

.note:hover {
  background-color: #f4f4f4;
  padding: 5px;
  transform: scale(1.08);
}

.create-entry {
  margin: 10px;
}

