.title-editor:hover {
  transform: scale(1.05);
}

.title-editor {
  border-radius: 10px;
  transition: all .2s ease-in-out;
  height: 150px;
  /* background-color: #b2cfff; */
  background-color: #ccf3ff;
  margin: 5px;
  box-shadow: 2px 2px 1px 1px #7e92b2;
}





.new-note-title {
  margin-left: 20px;
  margin-right: 20px;
  font-size: 30px;
  border: none;
}

.new-note-message {
  margin-left: 20px;
  margin-right: 20px;
  font-size: 30px;
}

.new-note-save-btn {
  margin-left: 5px;
  margin-top: 15px;
  font-size: 15px;
}

.timeline {}

.timeline-event {
  background-color: #fac564;
  padding-left: 2px;
  font-size: 15px;
  font-weight: bolder;
}

.timeline-event-col {}

.modal-popover {
  border-radius: 5px;
}

.modal-title {
  background-color: #7f0257;
  font-size: 18px;
  color: white;
  font-weight: bolder;
}

.modal-desc {
  background-color: #f4f4f4;
}
.card-title-pop{
  font-size: 15px;
  font-weight: bolder;
}
/* Best moment  */
.best-moment-editor:hover {
  transform: scale(1.05);
}
.best-moment-editor {
  border-radius: 10px;
  transition: all .2s ease-in-out;
  height: 150px;
  /* background-color: #2253a3; */
  background-color: #f9beb8;
  margin: 5px;
  box-shadow: 2px 2px 1px 1px #845550;
}
.best-moment-pop{
  background-color: #ffa39b;
  padding:8px;
  border-radius: 5px;
  transition: all .2s ease-in-out;
  margin-bottom: 5px;
}
.best-moment-body-pop{
  font-size: 15px;
  padding:5px;
}
.best-moment-pop:hover{
  transform: scale(1.03);
}
